  width: 45px;
}

* {
  box-sizing: border-box;
}

body {
  width: 100%;
  margin: 0px;
}

.nombrePag {
  color: white;
}

.cabecera {
  background-color: rgb(27, 37, 128);
  width: 100%;
  position: fixed;
}
.cabecera .listaCabecera {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  gap: 25px;
  width: 100%;
  /*border-bottom: 2px solid rgb(196, 182, 182);
  padding: 10px;*/
}

.menuNavegacionNav {
  /*ul {

     gap: 25px;
