}
.menuNavegacionNav li {
  /*text-decoration: underline #CC0000 3px;*/
  list-style-type: none;
  font-family: "Staatliches";
}
.menuNavegacionNav a {
  text-decoration: none;
  color: white;
}
.menuNavegacionNav .listaCabecera {
  font-size: 14px;
}
.menuNavegacionNav .enlaceRegistro {
  width: 50px;
  border-radius: 0.5em;
  padding: 6px;
  color: rgb(27, 37, 128);
  background-color: rgb(204, 166, 31);
}

.piePagina {
  background-color: rgb(27, 37, 128);
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 55px;
  padding: 10px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: center;
  gap: 15px;
}
.piePagina a {
  color: #ffffff;
  font-family: "Staatliches";
  font-size: 12px;
}
.piePagina .inviertePiePag {
  justify-self: flex-end;
}
.piePagina .iconoInsta {
  width: 18px;
}
.piePagina .iconoFacebook {
  width: 18px;
}
.piePagina .textoInstaPie {
  justify-self: flex-start;
}
.piePagina .textoFacebookPie {
  justify-self: center;
}

